.footer {
  font-family: inherit;
  box-sizing: border-box;
  font-weight: 400;
  line-height: 20px;
  @include contentPaddingInline;

  &__title {
    @include titleSize;
  }

  &__conteiner {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 24px;

    @include onDesctop {
      grid-template-columns: repeat(2, 1fr);
      gap: 24px;
    }
  }

  &__form {
    margin: 48px 0;
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include onTablet {
    margin: 56px 0 80px;
    }
  }

  &__input :nth-child(-n+3) {
    margin-bottom: 16px;
  }

  &__form__field {
    width: 100%;
    max-width: 600px;
    border-radius: 8px;
    background-color: #f8f8fa;
    color: #7e7e83;
    padding: 14px 0 14px;
    border: none;
    font-size: 14px;
    font-family: inherit;
  }

  &__form__text {
    color: #7e7e83;
    width: 100%;
    max-width: 600px;
    background-color: #f8f8fa;
    border-radius: 8px;
    border: none;
  }

  &__form__btn {
    @include forAllButtons;
    border: 2px solid #F0F1F4;

    font-size: 16px;
    line-height: 27px;
    text-align: center;

    padding: 14.5px 120px;
  }

  &__form__btn:hover {
    color: $main-accent;
    background-color: $secound-accent;
    border-color: 2px solid #f8f8fa;
  }

  &__form__btn:active {
    color: $secound-accent;
    background-color: $main-accent;
    border-color: #f8f8fa;
  }

  &__list {
    list-style: none;
    padding-bottom: 120px;
  }

  &__item {
    color: #9393a3;
    font-size: 14px;
    margin-bottom: 8px;
  }

  &__link {
    text-decoration: none;
  }

  &__descr {
    font-size: 16px;
    color: #191919;
    margin-bottom: 24px;
  }

  &__address {
    margin-bottom: 64px;
  }
}
